<template>
  <van-nav-bar :title="title" @click-left="leftArrowClick" @click-right="rightClick" fixed>
    <slot name="title" slot="title"></slot>
    <slot v-if="leftArrow && !$route.query.browerFirstIn" name="left" slot="left"><i class="icon-back"></i>{{ leftText }}</slot>
    <slot name="right" slot="right"></slot>
  </van-nav-bar>
</template>

<script>
// import utils from '@/utils/utils'
import UplusApi from '@uplus/uplus-api'
const instance = new UplusApi()
instance.initDeviceReady()
export default {
  name: 'Header',
  props: {
    text: {
      type: String,
      default: '',
    },
    leftText: {
      type: String,
      default: '',
    },
    leftArrow: {
      type: Boolean,
      default: true,
    },
    leftClick: {
      type: [Boolean, Function],
      default: false,
    },
    rightClick: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      title: this.text || document.title,
      leftArrowClick: null,
    }
  },
  created() {
    console.log('this.leftClick')
    console.log(this.leftClick)

    if (!this.leftClick) {
      this.leftArrowClick = () => {
        this.goBack()
      }
    } else {
      this.leftArrowClick = this.leftClick
    }
  },
  watch: {
    text(newVal) {
      this.title = newVal
    },
    $route(to, from) {
      let meta = to.meta
      if (meta.title) {
        this.title = meta.title
      } else if (!this.title) {
        this.title = document.title
      }

      // // 如果是收货地址部分，需要区分创建收货地址和编辑收货地址的title
      // const _href = window.location.href;
      // if (_href.indexOf('addressEdit') > 0) {
      //   this.title = _href.indexOf('addressEdit?id') > 0 ? '编辑收货地址' : '创建收货地址';
      // }
    },
  },
  mounted() {
    // const context = this;
    let meta = this.$route.meta
    if (meta.title) {
      this.title = meta.title
    }

    // 如果是收货地址部分，需要区分创建收货地址和编辑收货地址的title
    // const _href = window.location.href;
    // if (_href.indexOf('addressEdit') > 0) {
    //   context.title = _href.indexOf('addressEdit?id') > 0 ? '编辑收货地址' : '创建收货地址';
    // }
  },
  methods: {
    goBack() {
      this.$store.dispatch('doHistoryBack')
    },
  },
}
</script>
<style lang="scss" scoped>
.van-nav-bar__left {
  left: 0.24rem;
  display: flex;
  height: 100%;
  align-items: center;
}
.van-nav-bar__left, .van-nav-bar__right{
  img{
    -webkit-touch-callout: none;
    pointer-events: none;
  }
}
.van-nav-bar--fixed {
  z-index: 900;
  right: 0;
  @include max-width;
}

.icon-back {
  display: inline-block;
  width: 0.34rem;
  height: 0.34rem;
  background: url(data:image/png;base64,)
    no-repeat;
  background-size: contain;
}
</style>
