/**
 * 全局过滤器文件
 */
import timeUtil from './TimeUtil'
/**
  * @description  格式化价钱  保留2位小数
 */
export const formatPrice = (value) => {
  if ((value || value === 0) && value !== 'NaN') {
    return parseFloat(value).toFixed(2)
  } else {
    return '0'
  }
}

/**
  * @description 此处用的时间戳是秒级别，*1000之后再将时间戳格式化 返回2019-10-01 23:59:11
*/
export const formatDate = (value) => {
  if (value && value.toString() !== '0') {
    const nowDate = new Date(value * 1000);
    const _year = nowDate.getFullYear();
    let _month = nowDate.getMonth() + 1;
    _month = _month < 10 ? '0' + _month : _month;
    let _date = nowDate.getDate();
    _date = _date < 10 ? '0' + _date : _date;
    const _hour = nowDate.getHours() < 10 ? '0' + nowDate.getHours() : nowDate.getHours();
    const _minute = nowDate.getMinutes() < 10 ? '0' + nowDate.getMinutes() : nowDate.getMinutes();
    const _second = nowDate.getSeconds() < 10 ? '0' + nowDate.getSeconds() : nowDate.getSeconds();
    return _year + '-' + _month + '-' + _date + ' ' + _hour + ':' + _minute + ':' + _second;
  } else {
    return ''
  }
}

/**
  * @description  将时间格式化 返回 2019.10.01
*/
export const formatDateYMD = (value, join = '.') => {
  const nowDate = new Date(value);
  const _year = nowDate.getFullYear();
  let _month = nowDate.getMonth() + 1;
  _month = _month < 10 ? '0' + _month : _month;
  let _date = nowDate.getDate();
  _date = _date < 10 ? '0' + _date : _date;
  return _year + join + _month + join + _date;
}

/**
  * @description  格式化时间  2019-01-01
  * @param value 毫秒数
 */
export function formatTime(value) {
  return timeUtil.formatDate(new Date(value))
}

/**
 * 金额千分制，保留两位小数， 例100,000,0.00
 */
export function fmtMoneyFilter(val) {
  if ((val || val === 0) && val !== 'NaN') {
    return parseFloat(val).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,');
  } else {
    return '0'
  }
}

/**
 * 金额千分制，保留两位小数， 例100,000,0.00  但是只返回其中的整数部分
 */
export function fmtMoneyFilterInteger(val) {
  if ((val || val === 0) && val !== 'NaN') {
    const price = parseFloat(val).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,').split('.');
    return price[0]
  } else {
    return '0'
  }
}

/**
 * 金额保留两位小数， 例1000000.00  但是只返回其中的整数部分
 */
export function fmtMoneyFilterInt(val) {
  if ((val || val === 0) && val !== 'NaN') {
    const price = parseFloat(val).toFixed(2).split('.');
    return price[0]
  } else {
    return '0'
  }
}

/**
 * 金额千分制，保留两位小数， 例100,000,0.00  但是只返回其中的小数部分
 */
export function fmtMoneyFilterDecimal(val) {
  if ((val || val === 0) && val !== 'NaN') {
    const price = parseFloat(val).toFixed(2).replace(/(\d)(?=(\d{3})+\.)/g, '$1,').split('.');
    return `.${price[1]}`
  } else {
    return '.00'
  }
}

/**
 * 将图片url地址改为https
 * @param {string} url 图片url
 */
export function imageToHttps(url) {
  if (!url) {
    return
  }
  if (url.indexOf('http:') >= 0) {
    url = url.replace('http:', 'https:');
  }
  return url;
}

/**
 * 取选缩略图
 * @param {string} size 拼接的参数，如：100
 * @param {string} url 图片url
 */
export function imageThumb(url, size) {
  if (!url || !size) {
    return url
  }
  if (size) {
    if (url.indexOf('aliyuncs.com') > 0 || url.indexOf('cdn50.ehaier.com') > 0 || url.indexOf('cdn51.ehaier.com') > 0) {
      url = url.indexOf('?x-oss-process=image') > 0
        ? `${url}/resize,m_lfit,h_${size},w_${size}`
        : `${url}?x-oss-process=image/resize,m_lfit,h_${size},w_${size}`
    } else if (url.indexOf('ehaier.com') > 0) {
      url = `${url}@${size}_${size}`
    }
  }
  return url;
}

/**
 * 取webp
 * @param {string} url 图片url
 */
export function imageToWebp(url, webpSurpport) {
  if (!url || !webpSurpport) {
    return url
  }
  if (url.indexOf('aliyuncs.com') > 0 || url.indexOf('cdn50.ehaier.com') > 0 || url.indexOf('cdn51.ehaier.com') > 0) {
    url = url.indexOf('?x-oss-process=image') > 0
      ? `${url}/format,webp` : `${url}?x-oss-process=image/format,webp`
  }
  return url;
}

/**
  * @description 时间戳格式化 返回2019-10-01 23:59:11
*/
export const formatDateNew = (value) => {
  if (!value) {
    return ''
  }
  const date = new Date(value);
  const Y = date.getFullYear() + '.';
  const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '.';
  const D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
  const h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
  const m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
  const s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
  return Y + M + D + h + m + s;
}

/**
 * @description 正则替换富文本内img标签src属性
 * @param {string} richText 富文本字符串
 * @param {bool} webpSurpport 是否支持webp
 */
export function handleImgSrc(richText, webpSurpport) {
  let patten = /<img [^>]*src=['"]([^'"]+)[^>]*>/gi
  let arr = []
  let result = richText.replace(patten, (a, b) => {
    arr.push(b)
    return a.replace(b, imageToWebp(imageToHttps(b), webpSurpport));
  })
  return [result, arr]
}

/**
 * @description 截取价格小数点之前的文本
 * @param {float}} price 价格
 */
export function formatPriceInt(price) {
  return (price + '').split('.')[0]
}

/**
 * @description 截取价格小数点之后的文本
 * @param {float}} price 价格
 */
export function formatPriceFloat(price) {
  if ((price + '').includes('.')) {
    return '.' + (price + '').split('.')[1]
  }
  return '.00'
}
/**
 * @description 截取时间字符串 截止到日
 * @param {string}} value 日期字符串
 */
export function formatDateEndOfDay(value) {
  if (value.split(' ').length > 0) {
    return value.split(' ')[0]
  }
  return '1111-11-11'
}
