import axios from '@/http/http'
import URL from '@/http/url'
import UplusApi from '@uplus/uplus-api';
const instance = new UplusApi();
instance.initDeviceReady()
const defaultLocation = { // 默认地址
  region: '山东 青岛 崂山区 中韩街道',
  provinceId: 16,
  province: '山东',
  cityId: 173,
  city: '青岛',
  districtId: 2450,
  district: '崂山区',
  townshipId: 12036596,
  township: '中韩街道',
  latitude: '36.12784',
  longitude: '120.41803',
}

export default {
  namespaced: true,
  actions: {
    doLocation({ commit }) {
      return new Promise((resolve, reject) => {
        // axios.get('/aaa.json') // 调试
        instance.initDeviceReady().then(() => {
          // axios.get('/bbb.json') // 调试
          instance.upLocationModule.getLocation().then(res => {
            // axios.get('/ccc.json?res=' + JSON.stringify(res)) // 调试
            if (res.retData) {
              // let longitude = res.retData.latitude;
              // let latitude = res.retData.longitude
              let longitude = res.retData.longitude;
              let latitude = res.retData.latitude;

              resolve({ latitude, longitude })
            } else {
              resolve(400)
            }
          }).catch(() => {
            resolve(400)
          })
        })
      })
    },
    doLocationInfo({ commit }, params) {
      // axios.get('/eee.json') // 调试
      if (!params || params === 400) {
        return 400
      }
      return axios.get(URL.GET_LOCATION, {
        params: { latitude: params.latitude, longitude: params.longitude }
      }).then(res => {
        // axios.get('/fff.json?res=' + JSON.stringify(res)) // 调试
        const { success, data } = res.data
        if (success && data && data.province) {
          const region = `${data.province} ${data.city} ${data.district} ${data.township}`
          const location = {
            region,
            latitude: params.latitude,
            longitude: params.longitude,
            ...data,
          }
          // 仅展示 不存储
          if (params.noStore) {
            return { ...location }
          }
          commit('setLocationInfo', location, { root: true });
          commit('setLoaction', { latitude: params.latitude, longitude: params.longitude }, { root: true })
          localStorage.setItem('longitude', params.longitude);
          localStorage.setItem('latitude', params.latitude);
          localStorage.setItem('location', JSON.stringify(location));
          return { ...location }
        } else {
          if (!localStorage.getItem('location')) {
            commit('setLoaction', { latitude: '36.12784', longitude: '120.41803' }, { root: true })
            commit('setLocationInfo', defaultLocation, { root: true });
            localStorage.setItem('longitude', '120.41803');
            localStorage.setItem('latitude', '36.12784');
            localStorage.setItem('location', '');
          }
          return 400
        }
      }).catch(() => {
        return 400
      })
    }
  }
}
