  /**
   * @description 邮箱校验
  */
  isEmail(email) {
    return /^[.\w-]+@[\w-]+(.[a-zA-Z]+)+$/.test(email)
  },
  isPhone(phone) {
    return /^[1]([3-9])[0-9]{9}$/.test(phone)
  },
  isIdCard(id) {
    // return /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}$)/.test(id);
    return /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(id)
  },
  isBankCard(val) {
    return /^([1-9]{1})(\d{11}|\d{15}|\d{16}|\d{17}|\d{18})$/.test(val)
  },
  isNull(str) {
    if (str === 'null' || str === null || str === undefined || str === 'undefined' || str === '0' || str === 0) {
      return true
    } else {
      return false
    }
  },
  /**
   * @returns 是否期望隐藏导航栏
   */
  shouldHideNaviBar() {
    return sessionStorage.getItem('hide_navi') === '1'
  },
  /**
   * 是否为wifi状态
   */
  isWifi() {
    return new Promise((resolve) => {
      if (this.isHaierApp()) {
        try {
          instance.initDeviceReady().then(() => {
            instance.upNetworkModule.getNetworkInfo().then((result) => {
              console.log('result networkInfo ', result)
              if (result.retData.type === 'WiFi') {
                resolve(true)
              } else {
                resolve(false)
              }
            }).catch(() => {
              resolve(false)
            });
          })
        } catch (e) {
          resolve(false)
        }
      } else {
        try {
          let wifi = true
          let ua = window.navigator.userAgent
          let con = window.navigator.connection
          // 如果是微信
          if (/MicroMessenger/.test(ua)) {
            if (ua.indexOf('WIFI') >= 0) {
              resolve(true)
            } else {
              wifi = false
            }
            // 如果支持navigator.connection
          } else if (con) {
            let network = con.type
            if (network !== 'wifi' && network !== '2' && network !== 'unknown') {
              wifi = false
            }
          }
          resolve(wifi)
        } catch (e) {
          resolve(false)
        }
      }
    })
  },
