  injectISOAddressCodeToURL(url, locationInfo) {
    if (url && locationInfo && !url.includes('cityCode')) {
      let newUrl = url
      if (!url.includes('?')) {
        newUrl += '?'
      } else {
        newUrl += '&'
      }
      newUrl = `${newUrl}cityCode=${locationInfo.cityCode}&districtCode=${locationInfo.districtCode}&provinceCode=${locationInfo.provinceCode}&streetCode=${locationInfo.townshipCode}`
      return newUrl
    } else {
      return url
    }
  },
  formatNumber(number, decimals, dec = '.', sep = ',') {
    // 参数说明：
    // number：要格式化的数字
    // decimals：保留几位小数
    // dec：小数点符号
    // sep：千分位符号
    number = (number + '').replace(/[^0-9+-Ee.]/g, '');
    var n = !isFinite(+number) ? 0 : +number;
    var prec = !isFinite(+decimals) ? 2 : Math.abs(decimals);
    var s = '';
    var toFixedFix = function(n, prec) {
      var k = Math.pow(10, prec);
      return '' + Math.ceil(n * k) / k;
    };

    s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.');
    var re = /(-?\d+)(\d{3})/;
    while (re.test(s[0])) {
      s[0] = s[0].replace(re, '$1' + sep + '$2');
    }

    if ((s[1] || '').length < prec) {
      s[1] = s[1] || '';
      s[1] += new Array(prec - s[1].length + 1).join('0');
    }
    return s.join(dec);
  },
  imgToBase64(url, extOpts = { height: 0, width: 0, quality: 0 }) {
    return new Promise((resolve) => {
      var canvas = document.createElement('canvas');
      var ctx = canvas.getContext('2d');
      var img = new Image();
      img.crossOrigin = 'Anonymous';
      img.src = url;
      img.onload = function () {
        canvas.height = extOpts.height || img.height;
        canvas.width = extOpts.width || img.width;
        ctx.drawImage(img, 0, 0, extOpts.height || img.width, extOpts.width || img.height);
        var dataURL = canvas.toDataURL('image/png', extOpts.quality || 0.92);
        canvas = null;
        resolve(dataURL);
      };
      img.onerror = function() {
        resolve(false)
      }
    })
  },
  uplusSaveImage(base64URLArr) {
    let imgUrls = []
    if (base64URLArr instanceof Array) {
      base64URLArr.forEach(item => {
        imgUrls.push(item.split(';base64,')[1])
      });
    } else {
      imgUrls = [base64URLArr.split(';base64,')[1]]
    }
    return new Promise(resolve => {
      instance.upImageModule.saveImages({ images: imgUrls }).then(() => {
        resolve(true)
      }, () => {
        resolve(false)
      })
    })
  },
