  /**
   * 智家 and 掌柜 app 通用分享方法
   * @param {*} options
   * title 默认 海尔智家商城
   * desc 默认 海尔智家商城
   * url 默认 当前页面url
   * thumImage 默认 海尔智家商城logo
   *
   * update by wangpengfei 2021.5.13
   * 新增ihaier内部分享功能
   */
  inAppShare(options = {}) {
    if (this.isHaierApp()) {
      return this.uplusShareModule(options)
    } else if (this.isSgRn()) {
      return this.sgRnShareModule(options)
    } else if (this.isIhaierApp()) {
      return this.ihaierShareModule(options)
    } else {
      console.log('未知app分享')
    }
  },
  ihaierShareModule(options = {}) {
    return new Promise((resolve, reject) => {
      if (window.qing) {
        resolve(window.qing)
      } else {
        this.loadScript('//i.haier.net/public/js/qing/latest/qing.js').then(res => {
          if (window.qing) {
            resolve(window.qing)
          } else {
            resolve(false)
          }
        })
      }
    }).then(res => {
      if (window.qing) {
        let shareWay = ''
        switch (options.platformType) {
          case 'WechatSession':
            shareWay = 'wechat'
            break;
          case 'WechatTimeLine':
            shareWay = 'moments'
            break;
          case 'Sina':
            shareWay = 'weibo'
            break;
          case 'QQ':
            shareWay = 'qq'
            break;
          default:
            shareWay = 'wechat,moments'
            break;
        }
        console.log('options', options)
        this.imgToBase64(options.thumImage, {
          height: 100,
          width: 100,
          quality: 0.5
        }).then(res => {
          console.log(res)
          if (res === 'gg') {
            res = 'data:image/jpeg;base64,'
          }
          window.qing.call('socialShare', {
            shareType: 3,
            shareWay,
            shareContent: {
              title: options.title,
              description: options.desc,
              thumbData: res && res.replace(/^data:image\/\w+;base64,/, ''),
              shareIconUrl: options.thumImage,
              webpageUrl: options.url
            },
            success: res => {
              console.log('socialShare', res)
            }
          });
        })
        return window.qing
      } else {
        return false
      }
    })
  },
