  /**
   *
   * @param {*} options
   * platformType String 否 平台类型: Sina（新浪） WechatSession（微信聊天）WechatTimeLine（微信朋友圈） WechatFavorite（微信收藏) QQ（QQ） QQzone（QQ空间）
   * title	String	否	分享类型为webPage时，要分享的网页的标题 ，分享类型为image时不必填"标题"
   * desc	String	否	分享类型为webPage时，要分享的网页的描述信息 ，分享类型为image时不必填"描述信息"
   * url	String	否	分享类型为webPage时，要分享的网页的URl，分享类型为image时不必填
   * image	String	否	分享类型为image时的image路径,如 'http://', 'https://', 'file://',也可以为base64编码的字符串,分享类型为webPage时不必填
   * shareType	String	否	分享类型 webPage 或者 image, 请注意cordova 容器下只支持 webPage 类型
   * thumImage String	否	分享类型为webPage时的缩略图 如 'https://', 'file://'
   */
  uplusShareModule(options = {}) {
    const isSyBird = this.isSyBird()
    let defaultLogo = isSyBird ? 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/new_sybird_logo.png' : 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/uplus_logo.png'
    let url = options.url || window.location.href
    // 三翼鸟App分享的M站中需要携带open_source用来区分平台
    if (isSyBird) {
      if (url.includes('?')) {
        url = url + '&open_source=haier-app-sybird-shared-h5&custom_title=' + encodeURIComponent('三翼鸟')
      } else {
        url = url + '?open_source=haier-app-sybird-shared-h5&custom_title=' + encodeURIComponent('三翼鸟')
      }
    }
    if (options.platformType) {
      return new Promise((resolve, reject) => {
        instance.upShareModule.share({
          platformType: options.platformType,
          title: options.title || (isSyBird ? '定制智慧家 就找三翼鸟' : '海尔智家商城'),
          desc: options.desc || (isSyBird ? '定制智慧家 就找三翼鸟' : '海尔智家商城'),
          url: url,
          thumImage: options.thumImage ? options.thumImage.split('?')[0].replace('http:', 'https:') : defaultLogo,
          shareType: options.shareType || 'webPage',
          image: defaultLogo,
        }).then((result) => {
          if (result.retCode === '000000') {
            resolve(true)
          } else {
            resolve(false)
          }
        });
      })
    } else {
      return new Promise((resolve, reject) => {
        instance.upShareModule.showShareView({
          title: options.title || (isSyBird ? '定制智慧家 就找三翼鸟' : '海尔智家商城'),
          desc: options.desc || (isSyBird ? '定制智慧家 就找三翼鸟' : '海尔智家商城'),
          url: url,
          image: '',
          thumImage: options.thumImage ? options.thumImage.split('?')[0].replace('http:', 'https:') : defaultLogo,
          shareType: options.shareType || 'webPage',
        }).then((result) => {
          if (result.retCode === '000000') {
            resolve(true)
          } else {
            resolve(false)
          }
        });
      })
    }
  },
  /**
   *
   * @param {*} url 跳转地址
   * @param {*} flag 表示是否为完整url
   * @param {*} skip 表示是否跳过 拼接 container_type=3&show_title_bar=true&hidesBottomBarWhenPushed=1
   * @param {*} showheader 是否带原生头
   * @param {*} replace 是否替换url
   *
   */
  openNewWindow(url, flag, skip = false, showheader = true, replace = false) {
    let targetUrl = flag ? url : _config.HOST + url
    if (this.isHaierApp()) {
      if (!skip) {
        if (targetUrl.indexOf('#') !== -1) {
          let arr = targetUrl.split('#')
          let startStr = arr[0];
          let endStr = arr[1];
          if (arr[0].indexOf('?') !== -1) {
            startStr += `&container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          } else {
            startStr += `?container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          }
          targetUrl = startStr + '#' + endStr
        } else {
          if (targetUrl.indexOf('?') !== -1) {
            targetUrl += `&container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          } else {
            targetUrl += `?container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          }
        }
      }
      instance.initDeviceReady().then(() => {
        // instance.vdnModule.vdnEngine.goToPage({ url: targetUrl });
        instance.upVdnModule.goToPage({ url: encodeURI(targetUrl) });
      });
    } else {
      if (replace) {
        window.location.replace(targetUrl)
      } else {
        window.location.href = targetUrl
      }
    }
  },
  loadScript(url, elemId) {
    return new Promise((resolve, reject) => {
      if (elemId && document.getElementById(elemId)) {
        resolve();
        return;
      }
      var script = document.createElement('script')
      script.type = 'text/javascript';
      if (elemId) script.id = elemId
      if (script.readyState) { // IE
        script.onreadystatechange = function() {
          if (script.readyState === 'loaded' || script.readyState === 'complete') {
            script.onreadystatechange = null;
            resolve();
          }
        };
      } else { // Others
        script.onload = function() {
          resolve();
        };
      }
      script.src = url;
      document.getElementsByTagName('head')[0].appendChild(script);
    })
  },
  /**
   *
   * @param {*} 检测是否安装APP schema协议
   * @returns
   */
  testInstallApp(url, installCb, unInstallCb) {
    var timeout = null;
    var t = 1000; var hasApp = true;
    setTimeout(function () {
      if (hasApp) {
        installCb()
      } else {
        unInstallCb()
      }
      document.body.removeChild(ifr);
    }, 2000)

    var t1 = Date.now();
    var ifr = document.createElement('iframe');
    ifr.setAttribute('src', url);
    ifr.setAttribute('style', 'display:none');
    document.body.appendChild(ifr);
    timeout = setTimeout(function () {
      clearTimeout(timeout)
      var t2 = Date.now();
      if (!t1 || t2 - t1 < t + 100) {
        hasApp = false;
      }
    }, t);
  },
  numberToDouble(num) {
    return (num = num < 10 ? ('0' + num) : num);
  },
  winTorem() {
    (function(win) {
      var doc = win.document;
      var docEl = doc.documentElement;
      var tid;
      function refreshRem() {
        var width = docEl.getBoundingClientRect().width;
        if (width > 750) width = 750;
        var rem = width / 7.5;
        docEl.style.fontSize = rem + 'px';
      }
      win.addEventListener('resize', function() {
        clearTimeout(tid);
        tid = setTimeout(refreshRem, 300);
      }, false);
      win.addEventListener('pageshow', function(e) {
        if (e.persisted) {
          clearTimeout(tid);
          tid = setTimeout(refreshRem, 300);
        }
      }, false);
      refreshRem();
    })(window)
  },
  netWorkStatusHandler(onFn, offFn) {
    window.addEventListener('online', () => {
      onFn && onFn()
    }, false);
    window.addEventListener('offline', () => {
      offFn && offFn()
    }, false);
  },
