  dealSmDeviceId() {
    return new Promise(resolve => {
      var smTimeoutTime = 100;
      var smDeviceId = '';
      var smDeviceIdReady = false;
      var smTimer = setTimeout(function() {
        smDeviceId = window.SMSdk.getDeviceId ? window.SMSdk.getDeviceId() : smDeviceId;
        if (!smDeviceIdReady) {
          smDeviceIdReady = true; // 执行业务逻辑
          resolve(smDeviceId);
        }
      }, smTimeoutTime);
      window.SMSdk.ready(function() {
        smDeviceId = window.SMSdk.getDeviceId ? window.SMSdk.getDeviceId() : smDeviceId; clearTimeout(smTimer);
        if (!smDeviceIdReady) {
          smDeviceIdReady = true; // 执行业务逻辑
          resolve(smDeviceId);
        }
      });
    })
  },
  /**
   * 检查浏览器是否支持webp
   * @param {String} feature
   * 'feature' can be one of 'lossy', 'lossless', 'alpha' or 'animation'
   * @param {Func} callback
   */
  checkWebpFeature(feature, callback) {
    let kTestImages = {
      lossy: 'UklGRiIAAABXRUJQVlA4IBYAAAAwAQCdASoBAAEADsD+JaQAA3AAAAAA',
      lossless: 'UklGRhoAAABXRUJQVlA4TA0AAAAvAAAAEAcQERGIiP4HAA==',
      alpha: 'UklGRkoAAABXRUJQVlA4WAoAAAAQAAAAAAAAAAAAQUxQSAwAAAARBxAR/Q9ERP8DAABWUDggGAAAABQBAJ0BKgEAAQAAAP4AAA3AAP7mtQAAAA==',
      animation: 'UklGRlIAAABXRUJQVlA4WAoAAAASAAAAAAAAAAAAQU5JTQYAAAD/////AABBTk1GJgAAAAAAAAAAAAAAAAAAAGQAAABWUDhMDQAAAC8AAAAQBxAREYiI/gcA'
    };
    let img = new Image();
    img.onload = function () {
      let result = (img.width > 0) && (img.height > 0);
      callback(feature, result);
    };
    img.onerror = function () {
      callback(feature, false);
    };
    img.src = 'data:image/webp;base64,' + kTestImages[feature];
  },

  // 防抖
  debounce(func, delay = 300) {
    var timeout = null
    return function() {
      if (timeout !== null) { clearTimeout(timeout) }
      timeout = setTimeout(func.bind(this), delay)
    }
  },
  debounceImmediate(func, delay = 500, immediate = true) {
    var timeout;
    return function () {
      var context = this;
      var args = arguments;
      if (timeout) clearTimeout(timeout);
      if (immediate) {
        var callNow = !timeout;
        timeout = setTimeout(function() {
          timeout = null;
        }, delay)
        if (callNow) func.apply(context, args)
      } else {
        timeout = setTimeout(function() {
          func.apply(context, args)
        }, delay);
      }
    }
  },
  // 节流
  throttle(func, delay = 300) {
    var timer = null
    return function() {
      var context = this
      var args = arguments
      if (!timer) {
        timer = setTimeout(function() {
          func.apply(context, args)
          timer = null
        }, delay)
      }
    }
  },
  // 获取指定地址参数
  parseQueryValue(name) {
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
    let r = window.location.search.substr(1).match(reg);
    if (r != null) {
      // 对参数值进行解码
      let retVal = unescape(r[2])
      if (isNaN(retVal)) {
        retVal = 0
      }
      return retVal ? Number(retVal) : 0
    }
    return null;
  },
  // 获取指定地址参数
  parseURLQueryValue(url, name) {
    let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
    const strs = url.split('?')
    if (strs.length === 2) {
      url = '?' + strs[1]
    }
    let r = url.substr(1).match(reg);
    if (r != null) {
      // 对参数值进行解码
      let retVal = unescape(r[2])
      if (isNaN(retVal)) {
        retVal = 0
      }
      return retVal ? Number(retVal) : 0
    }
    return null;
  },

