  /** 微信端内H5支付
   *
   * @param {*} options
   * appId
   * timeStamp
   * nonceStr
   * package
   * signType
   * paySign
   */
  InWXH5Pay(options) {
    console.log('aaaaaaaaaaaaaaaaaaa', options)
    const onBridgeReady = () => {
      console.log(options, 'InWXH5Pay');
      // eslint-disable-next-line no-undef
      WeixinJSBridge.invoke(
        'getBrandWCPayRequest', {
          'appId': options.appId, // 公众号名称，由商户传入
          'timeStamp': options.timeStamp, // 时间戳，自1970年以来的秒数
          'nonceStr': options.nonceStr, // 随机串
          'package': options.package,
          'signType': options.signType || 'RSA', // 微信签名方式：
          'paySign': options.paySign // 微信签名
        },
        (res) => {
          if (res.err_msg === 'get_brand_wcpay_request:ok') {
            // 使用以上方式判断前端返回,微信团队郑重提示：
            // res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
            options.success && options.success(res)
          } else {
            options.fail && options.fail(res);
          }
        }
      );
    }

    if (typeof WeixinJSBridge === 'undefined') {
      console.log('11111111')
      if (document.addEventListener) {
        document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
      } else if (document.attachEvent) {
        document.attachEvent('WeixinJSBridgeReady', onBridgeReady);
        document.attachEvent('onWeixinJSBridgeReady', onBridgeReady);
      }
    } else {
      console.log('2222222')
      onBridgeReady();
    }
  },
  /**
 * @description 单位数前面加0
 * @param {*} n
 */
  formatTimeNumber (n) {
    n = n.toString()
    return n[1] ? n : `0${n}`
  },
  /**
 * @description 时间戳转化为年 月 日 时 分 秒
 * @param {Number} number 传入时间戳
 * @param {Sreing} format：返回格式，支持自定义，但参数必须与formateArr里保持一致
*/
  formatTime (number, format) {
    if (!number) {
      return ''
    }
    const formateArr = ['Y', 'M', 'D', 'h', 'm', 's']
    const returnArr = []

    const date = new Date(number)
    returnArr.push(date.getFullYear())
    returnArr.push(this.formatTimeNumber(date.getMonth() + 1))
    returnArr.push(this.formatTimeNumber(date.getDate()))

    returnArr.push(this.formatTimeNumber(date.getHours()))
    returnArr.push(this.formatTimeNumber(date.getMinutes()))
    returnArr.push(this.formatTimeNumber(date.getSeconds()))

    for (const i in returnArr) {
      format = format.replace(formateArr[i], returnArr[i])
    }
    return format
  },

  parseParamsToQuery(params) {
    let query = '?'
    if (params) {
      console.log('upHttpModule  处理 params', params)
      for (const [key, value] of Object.entries(params)) {
        if (value !== null && value !== undefined && value !== '') { // 数字 “0” 要保留, false 要保留
          query = query + key + '=' + value + '&'
        }
      }
      return query.substring(0, query.length - 1)
    }
    return ''
  },
  parseQueryToJson(query) {
    return Qs.parse(query);
  },
