import http from '../http'
import url from '../url'

export const test = (data) => {
  http.interceptors.request.use(
    function(config) {
      return config
    },
    function(error) {
      return Promise.reject(error)
    }
  )
  // 设置请求结束后拦截器
  http.interceptors.response.use(response => {
    return response
  })
  return http.request({
    baseURL: 'https://unidemo.dcloud.net.cn/',
    url: 'ajax/echo/text',
    dataType: 'text',
    method: 'get',
    data,
  })
}

