<template>
  <div v-show="showLoading" class="global-loading__wrapper" @click.stop>
    <div class="global-loading__main">
      <div class="circle"></div>
      <p class="text">{{message}}...</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'global-loading',
  data() {
    return {
      showLoading: false,
      message: '加载中'
    }
  },
  methods: {
    open(options = {}) {
      let { message = '加载中', delay = 0 } = options
      if (typeof message !== 'string') return console.error('message应为字符串类型')
      if (typeof delay !== 'number') return console.error('delay应为数字类型')
      this.message = message
      if (delay) {
        this.showLoading = true
        setTimeout(() => {
          this.showLoading = false
        }, delay)
      } else {
        this.showLoading = true
      }
    },
    close() {
      this.showLoading = false
    }
  }
}
</script>

